.list {
    margin: 15px;
}

label {
    display: block;
    padding: 8px 22px;
    margin: 0 0 5px 0;
    cursor: pointer;
    background: black;
    border-radius: 1rem;
    color: #FFF;
    transition: ease .5s;
    font-size: 20px;
}
.Checkbox{
    display: none;
}

label:hover {
    background: grey;
}

.content {
    background: white;
    padding: 1px 22px;
    border: 1px solid grey;
    margin: 0 0 1px 0;
    border-radius: 3px;
    font-size: 20px;
}


input + label + .content {
    display: none;
}

input:checked + label + .content {
    display: block;
    border: none;
}
